<template>
    <ul class="a1s">
        <li class="a1t" :class="{promotion:index < 3}" v-for="(good,index) in goods" :key="index">
            <!-- 一级菜单 -->
            <strong class="a1w a2 k isFirstCate"  @click="showitem(index)">
                <em v-if="index < 3" class="a1u a16"></em>
                <var class="a22">{{good.title}}</var>
            </strong>
            <!-- 一级菜单 -->

            <!-- 二级菜单 -->
            <strong v-if="good.lists" class="a1z" v-show="index==num">
                <span v-for="(list,index) in good.lists" class="a1y h a20" :key="index">{{list}}</span>
            </strong>
            <!-- 二级菜单 -->
        </li>
    </ul>
</template>

<script>
import $ from "jquery/dist/jquery.min.js";

export default {
  name: "pctg",
  created() {
    var baseUrl = "http://easy-mock.com/mock/59f6fe2dee3c3f29ab2fced0/daojia";
    var pctgUrl = baseUrl + "/pctg";
    var th = this;
    $.get(pctgUrl, { id: this.$route.params.id }, function(res) {
      for (let i = 0; i < res.goods.length; i++) {
        th.goods.push(res.goods[i]);
      }
    });
  },
  data() {
    return {
      goods: [],
      num: -1,
      zt: true
    };
  },
  methods: {
    showitem(index) {
      if (this.zt) {
        this.num = index;
        this.zt = !this.zt;
      } else {
        this.num = -1;
        this.zt = !this.zt;
      }
    }
  }
};
</script>

<style>

</style>



