    <div class="classify">
        <div class="ball5">
            <ball v-for="(ball,index) in ball5" :imgurl="ball.imgurl" :text="ball.text" :key="index"></ball>
        </div>
    </div> 
</template>

<script>
import ball from "./comForball5/ball.vue";

export default {
  name: "ball5",
  data: function() {
    return {
      ball5: [
        { imgurl: "1.png", text: "超市生鲜" },
        { imgurl: "2.png", text: "新鲜水果" },
        { imgurl: "3.png", text: "零食小吃" },
        { imgurl: "4.png", text: "鲜花烘焙" },
        { imgurl: "5.png", text: "医药健康" }
      ]
    };
  },
  components:{
      ball,
  }
};
</script>

