<template>
    <footer :class="'foot '+className ">
        <router-link v-for="(info,index) in infos" :key="index" :class="info.className" :to="info.to">{{info.text}}</router-link>
    </footer>
</template>

<script>
export default {
    name:'footer',
    data:function(){
      return {
        infos:[
          {className:'index',text:'首页',to:'/'},
          {className:'homeCategorySearch',text:'分类',to:'/classify'},
          {className:'cartList',text:'购物车',to:'/shoppingcar'},
          {className:'orderList',text:'订单',to:'/orderlist'},
          {className:'mine',text:'我的',to:'/mine'},
        ]
      }
    },
    props:['className'],
};
</script>

<style lang="scss">
/*-------foot部分----------*/
.foot {
    position: fixed;
    z-index: 801;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 50px;
    background: #fff;
    border-top: 1px solid #d9d9d9;
    display: flex;
    flex-direction: row;
    align-content: space-between;
    a {
      vertical-align: top;
      width: 20%;
      font-size: 12px;
      line-height: 17px;
      text-align: center;
      padding-top: 30px;
      height: 20px;
      background-image: url(/static/img/nav4.png);
      background-repeat: no-repeat;
      background-size: 24px 600px;
    }
  }
/*-------foot部分----------*/

</style>



