      ]
    };
  },
  methods: {
    show(index) {
      this.num = index;
    }
  }
};
</script>

<style lang="scss">
@function size($n) {
  @return $n/20;
}
html,
body {
  height: 100%;
}
a {
  text-decoration: none;
  color: #555;
}
li {
  list-style: none;
}
* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-weight: normal;
  font-style: normal;
}
img {
  border: 0;
}
body {
  margin: 0;
  padding: 0;
  font-family: "Microsoft YaHei", Verdana, Arial, Helvetica, sans-serif;
  color: #3c3c3c;
  font-size: 14px;
  line-height: 1.4;
  background-color: #f4f4f4;
  word-wrap: break-word;
}
div {
  .current {
    display: block;
    /*-------- 搜索 start --------*/
    .head {
      position: fixed;
      z-index: 12;
      left: 0;
      right: 0;
      top: 0;
      height: 45px;
      color: #555;
      font-size: 17px;
      text-align: center;
      line-height: 44px;
      background-color: #fff;
      .c-search {
        position: absolute;
        left: 8px;
        top: 8px;
        right: 8px;
        background: #fff;
        border-radius: 2px;
        border: 1px solid #e6e6e6;
        height: 28px;
        padding: 0 5px 0 20px;
        .icon {
          background-image: url(/static/img/search_bar_sprites.png);
          background-size: 44px auto;
          background-repeat: no-repeat;
        }
        .icon-search {
          position: absolute;
          top: 0;
          left: 0;
          background-position: -8px -8px;
          width: 44px;
          height: 44px;
        }
        .icon-clear {
          // display: none;
          position: absolute;
          top: 0;
          right: 0;
          background-position: -8px -140px;
          width: 28px;
          height: 28px;
        }
        input {
          padding: 3px 0;
          vertical-align: top;
          outline: none;
          background: transparent;
          border: none;
          font-family: "microsoft yahei", Verdana, Arial, Helvetica, sans-serif;
          font-size: 14px;
          height: 28px;
          box-sizing: border-box;
          width: 100%;
        }
      }
      .bg {
        display: none;
        line-height: 20px;
        font-size: 12px;
        text-align: left;
        position: absolute;
        top: 45px;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 9999;
        background: #f4f4f4;
        overflow: auto;
      }
    }
    /*-------- 搜索 end --------*/

    /*-------- 列表 start --------*/
    .c-lists {
      position: fixed;
      left: 0;
      top: 45px;
      bottom: 50px;
      width: 90px;
      z-index: 2;
      overflow: auto;
      -webkit-overflow-scrolling: touch;
      background-color: #f8f8f8;
      .c-list {
        height: 43px;
        padding-left: 14px;
        line-height: 44px;
        font-size: 14px;
        color: #666666;
        white-space: nowrap;
        border-bottom: 1px solid #e8e8e8;
        border-right: 1px solid #e8e8e8;
        background-color: #f8f8f8;
        text-overflow: ellipsis;
        overflow: hidden;
      }
      .chose {
        padding-left: 11px;
        color: #47b34f;
        background-color: #fff;
        border-left: 3px solid #47b34f;
        border-right-color: #fff;
      }
    }
    /*-------- 列表 end --------*/

    /*-------- 商品展示 start --------*/
    .hassearch {
      margin-top: 44px;
    }
    .scrolling {
      .content {
        .all {
          background-color: #fff;
          /*-------- 分类 start --------*/
          .show {
            padding-left: 95px;
            padding-bottom: 50px;
            background-color: #fff;
            -webkit-overflow-scrolling: touch;
            .lump {
              padding-top: 9px;
              .title {
                height: 19px;
                margin-top: 9px;
                margin-bottom: 9px;
                margin-left: 7%;
                line-height: 19px;
                font-size: 13px;
                color: #666666;
              }
              .displays {
                font-size: 0;
                .display {
                  display: inline-block;
                  width: 33%;
                  min-height: 94px;
                  margin-top: 6px;
                  img {
                    display: block;
                    width: 62px;
                    height: 62px;
                    margin: 0 auto;
                    background-image: url(/static/img/show_default.png);
                    background-size: 100%;
                    background-color: #f4f4f4;
                  }
                  .name {
                    display: block;
                    width: 100%;
                    text-align: center;
                    font-size: 12px;
                    color: #7d7d7d;
                    padding: 8px 0;
                  }
                }
              }
            }
          }
          /*-------- 分类 end --------*/
        }
      }
    }
    /*-------- 商品展示 end --------*/
    /*-------foot部分 start----------*/
    .foot-classify {
      .index {
        background-position: center -250px;
        color: #555;
      }
      .homeCategorySearch {
        background-position: center -50px;
        color: #39ac69;
      }
      .cartList {
        background-position: center -350px;
        color: #555;
      }
      .orderList {
        background-position: center -400px;
        color: #555;
      }
      .mine {
        background-position: center -450px;
        color: #555;
      }
    }
    /*-------foot部分 end----------*/
  }
}
</style>



