                    <span class="a1a">举报商家得红包</span>
                    最高奖100元
                    <strong class="a2g a16"></strong>
                </a>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    name:'info',
    props:['className','text','num','sell','people','time','dress','phone','seniority']
}
</script>

<style>

</style>



