<template>
    <router-link tag="li" :to="'/detail'+shop.shopid" class="shop">
        <div class="contents">
            <div class="content">
                <!-- logo -->
                <a href="javascript:void(0);" class="logo">
                    <span>
                        <img :src="'/static/upload/'+shop.logo.src">
                    </span>
                </a>
                <!-- logo -->
                <div class="detials">
                    <!-- part 1 -->
                    <router-link tag="a" :to="'/detail'+shop.shopid" class="introduce">
                        <!-- 店名 -->
                        <div class="name">
                            <h2 class="">{{shop.name}}</h2>
                            <div class="store_honor_box">
                                <!-- icon -->
                                <span :class="shop.icons.className"></span>
                                <!-- icon -->                                
                            </div>
                        </div>
                        <!-- 店名 -->

                        <!-- 配送 -->
                        <tagbar :time="shop.tagbar.time" :text="shop.tagbar.text" :className="shop.tagbar.className"></tagbar>
                        <!-- 配送 -->
                             
                        <!-- 评价 -->
                        <judge :num="shop.judge.num" :className="shop.judge.className"></judge>
                        <!-- 评价 -->

                        <!-- 配送信息 -->
                        <span class="explain">{{shop.sendmsg}}</span>
                        <!-- 配送信息 -->
                    </router-link>
                    <!-- part 1 -->

                    <!-- part 2 -->
                     <div>
                        <div class="some">
                        <!-- 虚线 -->
                            <span class="border"></span>
                        <!-- 虚线 -->

                        <!-- 优惠 -->
                          <div class="preferential">
                              <a href="javascript:void(0);" class="ticket">
                                  <ul>
                                      <activity v-if="index<num"  v-for="(act,index) in shop.activity" :key="index" :className="act.className" :text="act.text" :details="act.details"></activity>
                                  </ul>
                              </a>
                              <div v-if="shop.activity.length>2" :class="shop.more" @click="show()">
                                  <i :class="{position1:num == 100}"></i>
                              </div>
                          </div>
                          <!-- 优惠 -->

                        <!-- 列表 -->
                        <div class="lists">
                            <goods v-for="(good,index) in shop.goods" :key="index" :price="good.price" :text="good.text" :src="good.imgurl"></goods>
                        </div>
                        <!-- 列表 -->

                    </div>
                </div>
                <!-- part 2 -->
            </div>
        </div>
        </div>
        <!-- 标签 -->
        <span :class="shop.label"></span>
        <!-- 标签 -->
    </router-link>
</template>

<script>
import tagbar from "./comForShop/tagbar.vue";
import judge from "./comForShop/judge.vue";
import activity from "./comForShop/activity.vue";
import goods from "./comForShop/goods.vue";

export default {
  name: "shop",
  props: ["shop"],
  components: {
    tagbar,
    judge,
    activity,
    goods
  },
  data: function() {
    return {
      num: 2,
      zt: true
    };
  },
  methods: {
    show() {
      if (this.zt) {
        this.num = 100;
        this.zt = !this.zt;
      } else {
        this.num = 2;
        this.zt = !this.zt;
      }
    }
  }
};
</script>

<style lang="scss">

</style>



