import Vue from 'vue';
import app from './components/app'
import router from './router/index';
import "swiper/dist/css/swiper.min.css";
import store from "store/dist/store.everything.min.js";
import '../static/js/size.js';




Vue.config.productionTip = false;
Vue.use(router);

var vm = new Vue({
	el: '#app',
	template: '<app/>',
	data: {
		bus: new Vue(),
	},
	components: {
		app
	},
	created() {
		//初始化购物车
		if (!store.get('car')) {
			store.set('car', {});
		}
	},
	router,
});






