<template>
    <div>
        <section class="current">
            <article class="scrolling">
                <div class="content scroller">
                    <!-- 用户资料 -->
                    <div class="userdata">
                        <i class="headlog"></i>
                        <router-link tag="span" class="login" to="/login">登录／注册</router-link>
                        <div class="um">
                            <label class="QRcode">
                                <label class="QR"></label>
                            </label>
                            <label class="msg">
                                <i class="hideDot"></i>
                            </label>
                        </div>
                    </div>
                    <!-- 用户资料 -->

                    <!-- 钱包 -->
                    <div class="user-wallet">
                        <ul class="wallet">
                            <li class="walletdata">
                                <a href="">
                                    <i class="u5"></i>
                                    <b>我的钱包</b>
                                </a>
                            </li>
                            <li style="width:33%">
                                <a href="">
                                    <i class="t5">--</i>
                                    优惠券
                                </a>
                            </li>
                            <li style="width:33%">
                                <a href="">
                                    <i class="t5">--</i>
                                    白条
                                </a>
                            </li>
                        </ul>
                    </div>
                    <!-- 钱包 -->

                    <!-- 红包 -->
                    <div class="packet">
                        <advert :advert="adverts.advert1" :className="adverts.className1"></advert>                                                                
                    </div>
                    <!-- 红包 -->

                    <!-- 选项 -->
                    <div class="chooses">
                        <choose v-for="(choose,index) in chooses" :key="index" :text="choose.text" :className="choose.className" :imgurl="choose.imgurl"></choose>
                    </div>
                    <!-- 选项 -->

                    <!-- 退出 -->
                    <div class="leave">
                       <a class="tc" href="">退出账号</a> 
                    </div>   
                    <!-- 退出 -->
                </div>    
            </article>
            <!-- foot -->
            <foot :className="foots.className"></foot>
            <!-- foot -->
        </section>
    </div>
</template>

<script>
import advert from "./indexvue/advert";
import choose from "./minevue/choose";
import foot from "./footer";

export default {
  name: "mine",
  components: {
    advert,
    choose,
    foot
  },
  data: function() {
    return {
      foots: { className: "foot-mine" },
      adverts: {
        advert1: [{ imgurl: "27.png" }],
        className1: "swipe7 b2 t4"
      },
      chooses: [
        { className: "", imgurl: "i1.png", text: "我的关注" },
        { className: "", imgurl: "i2.png", text: "我的评价" },
        { className: "", imgurl: "i3.png", text: "我的地址" },
        { className: "", imgurl: "i4.png", text: "退款/售后" },
        { className: "", imgurl: "i5.png", text: "客服反馈" },
        { className: "", imgurl: "i6.png", text: "帮助中心" },
        { className: "", imgurl: "i7.png", text: "调查问卷" },
        { className: "news", imgurl: "i8.png", text: "办公室超市" },
        { className: "news bottom", imgurl: "i9.png", text: "配送员招募" },
        { className: "bottom", imgurl: "i10.png", text: "关于我们" }
      ]
    };
  }
};
</script>

<style lang="scss">
@function size($n) {
  @return $n/20;
}
html,
body {
  height: 100%;
}
a {
  text-decoration: none;
  color: #555;
}
li {
  list-style: none;
}
* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-weight: normal;
  font-style: normal;
}
img {
  border: 0;
}
body {
  margin: 0;
  padding: 0;
  font-family: "Microsoft YaHei", Verdana, Arial, Helvetica, sans-serif;
  color: #3c3c3c;
  font-size: 14px;
  line-height: 1.4;
  background-color: #f4f4f4;
  word-wrap: break-word;
}
div {
  .current {
    display: block;
    .scrolling {
      .content {
        /*---------用户资料---------*/
        .userdata {
          position: relative;
          padding-top: 24px;
          min-height: 91px;
          color: #fff;
          background: #fff url(/static/img/my_bgnew.png) center bottom no-repeat;
          background-size: cover;
          overflow: hidden;
          .headlog {
            background: url(/static/img/bg_head.png) no-repeat;
            background-size: auto 72px;
            -webkit-background-size: auto 72px;
            display: inline-block;
            width: 72px;
            height: 72px;
            margin: 0 0 0 16px;
            border-radius: 50px;
            vertical-align: middle;
            text-align: center;
          }
          .login {
            display: inline-block;
            height: 16px;
            padding: 7px 10px;
            color: #fff;
            border-radius: 2px;
            font-size: 14px;
            line-height: 16px;
            letter-spacing: 1px;
            background-color: #69c575;
            margin-left: 5px;
          }
          .um {
            position: absolute;
            left: 0;
            right: 0;
            top: 0;
            height: 50px;
            z-index: 20;
            .QRcode {
              z-index: 2;
              display: inline-block;
              position: absolute;
              right: 40px;
              top: -5px;
              padding: 20px 0 20px 20px;
              .QR {
                float: left;
                margin-right: 12px;
                width: 22px;
                height: 22px;
                background: url(/static/img/icon_er.png) no-repeat;
                background-size: 22px;
              }
            }
            .msg {
              display: inline-block;
              width: 22px;
              height: 22px;
              background: url(/static/img/user_message_icon.png) no-repeat;
              background-position: center center;
              background-size: 22px;
              position: absolute;
              right: 0;
              top: -5px;
              padding: 20px 10px 20px 20px;
              i {
                display: inline-block;
                width: 5px;
                height: 5px;
                background-color: #f00;
                position: absolute;
                right: 14px;
                top: 18px;
                border-radius: 5px;
              }
              .hideDot {
                display: none;
              }
            }
          }
        }
        /*---------用户资料---------*/

        /*---------钱包---------*/
        .user-wallet {
          height: 70px;
          margin-bottom: 10px;
          padding-left: 15px;
          background-color: white;
          .wallet {
            padding-top: 20px;
            padding-bottom: 12px;
            position: absolute;
            left: 0;
            top: 103px;
            width: 100%;
            z-index: 15;
            background: url(/static/img/user_wallet_bg.png) no-repeat;
            background-size: 100%;
            background-position: 0 0;
            .walletdata {
              position: relative;
              text-align: center;
              &::after {
                height: 100%;
                content: "";
                width: 3px;
                position: absolute;
                top: 0;
                right: -1px;
                transform: scaleX(0.5);
                -webkit-transform: scaleX(0.5);
                z-index: 2;
                background: url(/static/img/packet-right-border@2x.png)
                  no-repeat;
                background-size: 3px 44px;
              }
            }
            li {
              width: 30%;
              display: inline-block;
              a {
                line-height: 25px;
                text-align: center;
                font-size: 12px;
                display: block;
                position: relative;
                color: #323232;
                .u5 {
                  position: relative;
                  left: 50%;
                  background: url(/static/img/icon_packet.png) no-repeat;
                  background-size: 25px 25px;
                  display: block;
                  width: 25px;
                  height: 25px;
                  margin-left: -12px;
                }
                .t5 {
                  display: block;
                  height: 25px;
                  line-height: 25px;
                  font-size: 18px;
                  color: #323232;
                }
              }
            }
          }
        }
        /*---------钱包---------*/

        /*---------红包---------*/
        .packet {
          padding-left: 0;
          margin-bottom: 10px;
          background-color: white;
          .t4 {
            width: 100%;
            height: 75px;
          }
          .b2 {
            margin-top: 0;
          }
          .swipe7 {
            position: relative;
            z-index: 8;
            white-space: nowrap;
            text-align: left;
            overflow: hidden;
            a {
              width: 100%;
            }
            .swipe7list {
              position: relative;
              .swipe7item {
                position: relative;
                display: inline-block;
                vertical-align: top;
                width: 100%;
                text-align: center;
                a {
                  display: block;
                  position: relative;
                  color: #323232;
                  font-size: 13px;
                  line-height: 44px;
                  img {
                    display: block;
                    width: 100%;
                    height: 75px;
                  }
                }
              }
            }
          }
        }
        /*---------红包---------*/

        /*---------选项---------*/
        .chooses {
          padding-left: 0;
          font-size: 0;
          margin-bottom: 10px;
          a {
            display: block;
            position: relative;
            color: #323232;
            font-size: 13px;
            line-height: 44px;
          }
          background-color: white;
          .t6 {
            display: inline-block;
            padding: 25px 0;
            width: 25%;
            border-bottom: 1px solid #e8e8e8;
            text-align: center;
            line-height: 35px;
            .user-list-icon1 {
              background-size: 25px 25px;
              display: block;
              width: 25px;
              height: 25px;
              margin: 0 auto;
            }
          }
          .news {
            position: relative;
            &::after {
              content: " ";
              position: absolute;
              border: 2px solid #ff5757;
              width: 0;
              -webkit-border-radius: 100%;
              border-radius: 100%;
              top: 20px;
              right: 34%;
            }
          }
          .bottom {
            border-bottom: none;
          }
        }
        /*---------选项---------*/

        /*---------退出---------*/
        .leave {
          padding-bottom: 64px;
          .tc {
            display: block;
            visibility: hidden;
            height: 46px;
            color: #ff5757;
            text-align: center;
            font-size: 16px;
            line-height: 46px;
            background-color: #fff;
          }
        }
        /*---------退出---------*/
      }
    }
    /*---------foot---------*/
    .foot-mine {
      .index {
        background-position: center -250px;
        color: #555;
      }
      .homeCategorySearch {
        background-position: center -300px;
        color: #555;
      }
      .cartList {
        background-position: center -350px;
        color: #555;
      }
      .orderList {
        background-position: center -400px;
        color: #555;
      }
      .mine {
        background-position: center -200px;
        color: #39ac69;
      }
    }
    /*---------foot---------*/
  }
}
</style>



