<template>
    <div :class="'swipe7 '+className">
        <ul class="swipe7list swiper-wrapper">
            <li class="swipe7item swiper-slide" v-for="(advertitem,index) in advert" :key="index">
                <a href="">
                    <img :src="'/static/upload/'+advertitem.imgurl">
                </a>
            </li>
        </ul>
        <div class="swiper-pagination" v-if="advert.length>1"></div>
    </div>
</template>

<script>
import Swiper from "swiper/dist/js/swiper.min.js";

export default {
  name: "advert",
  props: ["advert", "className"],
  components: {},
};
</script>

<style lang="scss">
/*------------轮播图-------------*/
.swipe7 {
  position: relative;
  z-index: 8;
  white-space: nowrap;
  text-align: left;
  overflow: hidden;
  .swipe7list {
    position: relative;
    .swipe7item {
      position: relative;
      display: inline-block;
      vertical-align: top;
      text-align: center;
      a {
        display: block;
        width: 100%;
        img {
          display: block;
          width: 100%;
        }
      }
    }
  }
  /*------------分页符-------------*/
  .swiper-pagination {
    height: 10px;
    position: absolute;
    z-index: 3;
    left: 0;
    right: 0px;
    bottom: 6px;
    text-align: center;
    .swiper-pagination-bullet {
      display: inline-block;
      vertical-align: top;
      width: 3px;
      height: 3px;
      border: 2px solid #fff;
      border-radius: 8px;
      margin: 2px 2px;
      opacity: 1;
      background: none;
    }
    .swiper-pagination-bullet-active {
      background: #fff;
      border-color: #fff;
    }
  }
  /*------------分页符-------------*/
}
/*------------轮播图-------------*/
</style>



