          top: 0;
          width: 80px;
          height: 48px;
          background: url(/static/img/cart.png) 10px 5px no-repeat;
          background-size: 50px 125px;
          transition: transform 0.5s ease-out 0.1s;
          -webkit-transition: -webkit-transform 0.5s ease-out 0s;
        }
        .show {
          visibility: visible;
        }
        .light {
          top: -13px;
          height: 58px;
          background-position: 10px -70px;
          background-size: 50px 125px;
