            overflow: hidden;
            text-align: center;
            color: #fff;
            background-color: #ff5d5c;
            border-radius: 7px;
            font-size: 10px;
          }
        }
        .incartcontent {
          top: -63px;
        }
        .a5v {
          position: absolute;
          left: 29px;
          top: -7px;
          width: 11px;
          height: 7px;
          background: url(/static/img/a.png) 0 0 no-repeat;
          background-size: 11px 7px;
        }
        /*----------全选&清空-----------*/
