            position: absolute;
            left: 0;
            background-image: url(/static/img/ck.png);
            background-size: 20px 220px;
            background-repeat: no-repeat;
          }
          .a73 {
            padding-left: 40px;
            padding-right: 20px;
            top: 0;
            background-position: 10px 10px;
            color: #333;
          }
          .checked {
            background-position: 10px -190px;
          }
          .a75 {
            position: absolute;
            left: 74px;
            top: 1px;
            font-size: 12px;
            color: #666;
          }
          .a5x {
            position: absolute;
            right: 0;
            top: 0;
            padding: 0 12px 0 18px;
            background: url(/static/img/delete.png) 0 center no-repeat;
            background-size: 13px 12px;
            color: #666;
          }
        }
        // 有商品时
        /*----------全选&清空-----------*/

        /*----------购物车商品列表-----------*/
        .a5z {
          background-color: #fbfbfb;
          overflow: scroll;
          -webkit-overflow-scrolling: touch;
          .a6a {
            position: relative;
            ul {
              .a6c {
                position: relative;
                padding: 0 0 10px 30px;
                margin-left: 10px;
                line-height: 36px;
                color: #333;
                border-bottom: 1px solid #e8e8e8;
                .a72 {
                  position: absolute;
                  left: 0;
                  background-image: url(/static/img/ck.png);
                  background-size: 20px 220px;
                  background-repeat: no-repeat;
                }
                .a6d {
                  position: absolute;
                  z-index: 3;
                  left: -10px;
                  top: 6px;
                  width: 50px;
                  height: 60px;
                  background-position: 10px 20px;
                }
                .checked {
                  background-position: 10px -180px;
                }
                .a6j {
                  display: block;
                  .a6k {
                    table-layout: fixed;
                    width: 100%;
                    border-collapse: collapse;
                    td {
                      padding: 0;
                      margin: 0;
                      text-align: left;
                      vertical-align: top;
                      position: relative;
                      height: 62px;
                      .a6l {
                        width: 52px;
                        height: 52px;
                        margin-top: 10px;
                        vertical-align: top;
                      }
                      .a6o {
                        min-height: 36px;
                        line-height: 36px;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        white-space: nowrap;
                      }
                      .a6x {
                        line-height: 0;
                        .a6s {
                          display: inline-block;
                          vertical-align: top;
                          color: #fff;
                          margin: 2px 3px 0 0;
                          border-radius: 2px;
                          padding: 1px 2px;
                          line-height: 12px;
                          height: 12px;
                          overflow: hidden;
                          font-size: 10px;
                        }
                        .a6e {
                          line-height: 20px;
                          color: #ff3434;
                          font-size: 16px;
                          .a6f {
                            padding-left: 5px;
                            color: #999;
                            font-size: 13px;
                          }
                        }
                      }
                    }
                  }
                  .a6m {
                    right: 61px;
                    background-position: center -68px;
                  }
                  .a6p {
                    position: absolute;
                    top: 22px;
                    right: 35px;
                    width: 39px;
                    line-height: 52px;
                    height: 52px;
                    overflow: hidden;
                    text-align: center;
                    font-size: 16px;
                  }
                  .a6n {
                    right: 0;
                    background-position: center 12px;
                  }
                }
              }
            }
          }
        }
        /*----------购物车商品列表-----------*/
      }
      /*----------蒙版-----------*/
      .a5o {
        display: none;     
        position: fixed;
        z-index: 10;
        left: 0;
        right: 0;
        bottom: 0;
        top: 0;
        background-color: rgba(0, 0, 0, 0.4);
      }
      .dis{
        display: block;
      }
      /*----------蒙版-----------*/

      /*----------购物车列表-----------*/
    }
    /*----------结算-----------*/
  }
}
.a1a {
  float: left;
  padding-left: 18px;
  background-position: -58px -234px;
}
.a2::before {
  height: 1px;
  content: "";
  width: 100%;
  border-bottom: 1px solid #e8e8e8;
  position: absolute;
  bottom: -1px;
  right: 0;
  transform: scaleY(0.5);
  -webkit-transform: scaleY(0.5);
  z-index: 10;
}
.k::after {
  height: 100%;
  content: "";
  width: 1px;
  border-right: 1px solid #e8e8e8;
  position: absolute;
  top: 0;
  right: -1px;
  transform: scaleX(0.5);
  -webkit-transform: scaleX(0.5);
  z-index: 2;
}
</style>



