<template>
    <div>
        <section class="a7 current">
            <article class="scrolling">
                <div class="content scroller">
                    <div class="x6" style="min-height:568px">
                        <div class="sg">
                            <p>登录后才能查看订单哦</p>
                            <router-link class="Login" to="/login">立即登录</router-link>
                        </div>
                    </div>
                </div>
            </article> 
            <!-- foot -->
            <foot :className="foots.className"></foot>
            <!-- foot -->
        </section>
    </div>
</template>

<script>
import foot from "./footer";
export default {
  name: "orderlist",
  components: {
    foot
  },
  data: function() {
    return {
      foots: { className: "foot-orderlist" }
    };
  }
};
</script>

<style lang="scss">
@function size($n) {
  @return $n/20;
}
html,
body {
  height: 100%;
}
a {
  text-decoration: none;
  color: #555;
}
li {
  list-style: none;
}
* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-weight: normal;
  font-style: normal;
}
img {
  border: 0;
}
body {
  margin: 0;
  padding: 0;
  font-family: "Microsoft YaHei", Verdana, Arial, Helvetica, sans-serif;
  color: #3c3c3c;
  font-size: 14px;
  line-height: 1.4;
  background-color: #f4f4f4;
  word-wrap: break-word;
}

div {
  .current {
    display: block;
  }
  .a7 {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    .scrolling {
      -webkit-overflow-scrolling: touch;
      overflow: auto;
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      .scroller {
        overflow: auto;
        height: 100%;
        .x6 {
          background: url(/static/img/unlogin.png) no-repeat center;
          background-size: 96px;
          -webkit-transform: translateY(-90px);
          transform: translateY(-90px);
          position: relative;
          .sg {
            position: absolute;
            top: 50%;
            padding-top: 63px;
            text-align: center;
            width: 100%;
            font-size: 15px;
            line-height: 1;
            color: #666;
            .Login {
              display: inline-block;
              margin-top: 8px;
              padding: 10px 8px;
              border-radius: 3px;
              border: 1px solid #47b34f;
              color: #47b34f;
              background: #fff;
              line-height: 1;
            }
          }
        }
      }
    }
    /*---------foot---------*/
    .foot-orderlist {
      .index {
        background-position: center -250px;
        color: #555;
      }
      .homeCategorySearch {
        background-position: center -300px;
        color: #555;
      }
      .cartList {
        background-position: center -350px;
        color: #555;
      }
      .orderList {
        background-position: center -150px;
        color: #39ac69;
      }
      .mine {
        background-position: center -450px;
        color: #555;
      }
    }
    /*---------foot---------*/
  }
}
</style>



