<template>
    <div>
        <section class="current">
            <article class="scrolling">
                <div class="content scroller">
                    <div class="a4v" style="background-color: rgb(255, 255, 255);">
                        <table class="c" style="background-color:#fff; z-index:5;">
                            <tbody>
                                <tr>
                                    <td>
                                        <div class="a57">购物车还空空如也哦~<br>登录后将同步购物车中商品<br>
                                            <router-link class="a58" to="/login">立即登录</router-link>
                                        </div>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </article>
            <!-- foot -->
            <foot :className="foots.className"></foot>
            <!-- foot -->
        </section>        
    </div>
</template>

<script>
import foot from "./footer";

export default {
  name: "shoppingcar",
  components:{
      foot,
  },
  data: function() {
    return {
      foots: { className: "foot-shoppingcar" }
    };
  },
};
</script>

<style lang="scss">
@function size($n) {
  @return $n/20;
}
html,
body {
  height: 100%;
}
a {
  text-decoration: none;
  color: #555;
}
li {
  list-style: none;
}
* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-weight: normal;
  font-style: normal;
}
img {
  border: 0;
}
body {
  margin: 0;
  padding: 0;
  font-family: "Microsoft YaHei", Verdana, Arial, Helvetica, sans-serif;
  color: #3c3c3c;
  font-size: 14px;
  line-height: 1.4;
  background-color: #f4f4f4;
  word-wrap: break-word;
}

div {
  .current {
    display: block;
    .scrolling {
      .scroller {
        .a4v {
          padding: 0 0 99px 0;
          text-align: center;
          .c {
            table-layout: fixed;
            border-collapse: collapse;
            position: fixed;
            z-index: 2000;
            left: 0;
            top: 0;
            right: 0;
            bottom: 0;
            width: 100%;
            height: 100%;
            td {
              text-align: center;
              vertical-align: middle;
              .a57 {
                padding: 100px 0 160px 0;
                line-height: 20px;
                font-size: 15px;
                color: #666;
                text-align: center;
                font-size: 14px;
                background: url(/static/img/unlogin.png) center top no-repeat;
                background-size: 96px 96px;
                .a58 {
                  display: inline-block;
                  vertical-align: top;
                  padding: 0 8px;
                  line-height: 35px;
                  color: #fff;
                  background-color: #47b34f;
                  color: #fff;
                  font-size: 15px;
                  margin-top: 16px;
                  border-radius: 3px;
                }
              }
            }
          }
        }
      }
    }
    /*---------foot---------*/
    .foot-shoppingcar {
      .index {
        background-position: center -250px;
        color: #555;
      }
      .homeCategorySearch {
        background-position: center -300px;
        color: #555;
      }
      .cartList {
        background-position: center -100px;
        color: #39ac69;
      }
      .orderList {
        background-position: center -400px;
        color: #555;
      }
      .mine {
        background-position: center -450px;
        color: #555;
      }
    }
    /*---------foot---------*/
  }
}
</style>



