<template>
    <div class="show" style="min-height:428px">
        <dl class="lump" v-for="(item,index) in lump" :key="index">
            <dt class="title">{{item.title}}</dt>
            <dd class="displays">
                <display v-for="(display,index) in item.displays" :key="index" :name="display.name" :imgurl="display.imgurl"></display>
            </dd>
        </dl>
    </div>
</template>

<script>
import display from './forlump/display';
export default {
    name:'lump',
    props:['lump'],
    components:{
        display,
    }
};
</script>

<style>

</style>



