                              ul {
                                li {
                                  margin-top: 10px;
                                  font-size: 10px;
                                  position: relative;
                                  color: #999999;
                                  -webkit-text-size-adjust: none;
                                  text-overflow: ellipsis;
                                  white-space: nowrap;
                                  overflow: hidden;
                                  i {
                                    display: inline-block;
                                    padding: 0 2px;
                                    margin-right: 4px;
                                    border-radius: 2px;
                                    color: #ffffff;
                                    height: 14px;
                                    line-height: 14px;
                                  }
                                  .lingquan {
                                    background: #fa3e2d;
                                  }
                                  .manjian {
                                    background: #5fbc65;
                                  }
                                  .shoudan{
                                    background: #FF6C57
                                  }
                                  .manzhe{
                                    background: #72bb00;
                                  }
                                  .yunfei{
                                    background: #5CA7FF;
                                  }
                                  .maizeng{
                                    background: #fe332f;
                                  }
                                  .fanquan{
                                    background: #ffa800;
                                  }
                                }
                              }
                            }
                            .more {
                              position: absolute;
                              top: 3px;
                              right: -10px;
                              width: 14%;
                              font-size: 10px;
                              vertical-align: top;
                              text-align: right;
                              padding-top: 24px;
                              height: 40px;
                              margin-top: -16px;
                              padding-right: 10px;
                              i {
                                background: url(/static/img/index.png) no-repeat;
                                background-size: 20px 200px;
                                display: inline-block;
                                width: 18px;
                                height: 10px;
                                text-align: right;
                                background-position: 2px -183px;
                              }
                              .position1{
                                background-position: 2px -163px; 
                              }
                            }
                          }
                          .lists {
                            font-size: 0;
                            vertical-align: top;
                            white-space: nowrap;
                            padding-top: 12px;
                            .list {
                              position: relative;
                              display: inline-block;
                              overflow: hidden;
                              width: 25%;
                              text-align: center;
                              font-size: 0;
                              vertical-align: top;
                              max-height: 105px;
                              .item {
                                width: size(52rem);
                                height: size(52rem);
                                position: relative;
                                display: block;
                                border: 1px solid #f7f7f7;
                                border-radius: 3px;
                                img {
                                  display: inline-block;
                                  width: 100%;
                                }
                                .hot {
                                  position: absolute;
                                  left: 0;
                                  top: 0;
                                  padding: 1px 2px;
                                  font-size: 9px;
                                  color: #fff;
                                  background-color: #fc2c50;
                                }
                              }
                              .price {
                                color: #ff5757;
                                margin-top: 3px;
                                font-size: 12px;
                                height: 17px;
                                line-height: 17px;
                                vertical-align: top;
                                display: block;
                                width: 62px;
                                overflow: hidden;
                                white-space: nowrap;
                                text-overflow: ellipsis;
                              }
                            }
                          }
                        }
                      }
                      /*--------part 2------*/
                    }
                    /*--------详情------*/
                  }
                }
                /*--------内容------*/
              }
              /*--------店铺------*/
            }
          }
        }
        /*--------附近店铺--------*/
      }
      .hasbottom {
        // padding-bottom: 100px;
      }
    }
  }
  /*-------内容部分----------*/

  /*-------foot部分----------*/
  .foot-index {
    .index {
      background-position: center 0;
      color: #39ac69;
    }
    .homeCategorySearch {
      background-position: center -300px;
      color: #555;
    }
    .cartList {
      background-position: center -350px;
      color: #555;
    }
    .orderList {
      background-position: center -400px;
      color: #555;
    }
    .mine {
      background-position: center -450px;
      color: #555;
    }
  }
